.header {
  width: 100%;
}

.header .content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  padding: 2.5em 0;
}

@media (max-width: 960px) {
  .header .content {
    padding: 1.5em 0;
  }
}

.btn--menu {
  grid-column-start: 3;
  justify-self: end;
  display: flex;
  align-items: center;
  font-size: 1em;
  color: #3f51b5;
}

@media (max-width: 960px) {
  .btn--menu .helper-text {
    display: none;
  }
}

.icon--menu {
  fill: #3f51b5;
  width: 40px;
  height: 40px;
}
