.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.js .menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50vh;
}

.btn--menu-close {
  font-size: 2em;
  position: absolute;
  top: 1.25em;
  right: 1.25em;
  display: none;
}

.js .btn--menu-close {
  display: block;
}

/* Dummy pages behind the main element that will animate like cards */
.page {
  position: relative;
  perspective: 1200px;

  perspective-origin: 50% -50%;
}

.page__folder--dummy {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.page__folder:first-child {
  background: #5c6bc0;
}

.page__folder:nth-child(2) {
  background: #9fa8da;
}

.page__folder:nth-child(3) {
  background: #e8eaf6;
}

/************************/
/* Transitions 			*/
/************************/

.js .page--move {
  pointer-events: none;
}

.js .page__folder {
  transition: transform 1s;
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

.js .page__folder--dummy {
  transform: translate3d(0px, 100vh, 0);
}

.page--move .page__folder:first-child {
  transform: translate3d(0px, 75vh, -300px);
}

.page--move .page__folder:nth-child(2) {
  transform: translate3d(0px, 75vh, -250px);
}

.page--move .page__folder:nth-child(3) {
  transform: translate3d(0px, 75vh, -200px);
}

.page--move .page__folder:nth-child(4) {
  transform: translate3d(0px, 75vh, -150px);
}

.js .menu {
  pointer-events: none;
}

.js .menu--open {
  pointer-events: auto;
}

/* Close button */
.btn--menu-close {
  opacity: 0;
  transform: scale3d(0.8, 0.8, 1);
  transition: opacity 0.5s, transform 0.5s;
}

.menu--open .btn--menu-close {
  opacity: 1;
  transform: scale3d(1, 1, 1);
}

@media screen and (max-width: 40em) {
  .btn--menu-close {
    font-size: 1.25em;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: "Inconsolata", "Monaco", monospace;
  overflow: hidden;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: #22295f;
  background-repeat: no-repeat;
  background-position: 50% 10em;
  background-size: 75% auto;
}

/* Buttons */

.btn {
  margin: 0;
  padding: 0;
  cursor: pointer;
  border: none;
  background: none;
}

.btn:focus {
  outline: none;
}

.btn--hidden {
  pointer-events: none;
  opacity: 0;
}

/* Links */

a,
.btn {
  text-decoration: none;
  outline: none;
}

.hidden {
  position: absolute;
  overflow: hidden;
  width: 0;
  height: 0;
  pointer-events: none;
}

/* Icons */

.icon {
  display: block;
  width: 1.5em;
  height: 1.5em;
  margin: 0 auto;
  fill: currentColor;
}

/* Bottom nav */

.bottom-nav {
  position: relative;
  width: 100%;
  margin: auto 0 0 0;
  padding: 2.5em 2.75em 2.5em 2em;
}

/* Demo links */

.codrops-demos {
  font-weight: bold;
  padding: 1.25em 0;
  text-align: right;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
}

.codrops-demos span {
  display: inline-block;
  margin: 0 1em 0 0;
  vertical-align: bottom;
}

.codrops-demos a {
  position: relative;
  display: inline-block;
  margin: 0 0.1em;
  padding: 0 0.4em;
}

.codrops-demos a.current-demo::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2.25em;
  height: 2.25em;
  margin: -1.125em 0 0 -1.125em;
  border: 2px solid;
  border-radius: 50%;
}

/* Top Navigation Style */

.codrops-links {
  position: relative;
  display: flex;
  justify-content: center;
  text-align: center;
  white-space: nowrap;
}

.codrops-links::after {
  content: "";
  position: absolute;
  top: 15%;
  left: 50%;
  width: 2px;
  height: 70%;
  opacity: 0.1;
  background: currentColor;
  transform: rotate3d(0, 0, 1, 22.5deg);
}

.codrops-icon {
  display: inline-block;
  margin: 0.5em;
  padding: 0.5em;
}

/* Demo colors */
.demo-4 {
  background-color: #1a237e;
}
.demo-4 main {
  background-color: #ffffff;
}
.demo-4 .btn--menu-close {
  color: #e8eaf6;
}

.nav-links {
  display: flex;
}

@media (max-width: 960px) {
  .nav-links {
    flex-direction: column;
    align-items: center;
  }
}

.nav-links a {
  margin: 0 30px;
  color: #e8eaf6;
  display: flex;
  align-items: center;
  font-size: 16px;
}

@media (max-width: 960px) {
  .nav-links a {
    font-size: 12px;
  }
}

.nav-links svg {
  fill: #9fa8da;
  width: 23px;
  margin-left: 10px;
}

.nav-links button {
  display: flex;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  font-family: inherit;
  color: #e8eaf6;
}

@media (max-width: 960px) {
  .nav-links button {
    font-size: 12px;
  }
}

.btn__email {
  position: relative;
}

.btn__email::before,
.btn__email::after {
  position: absolute;
  left: 50%;
  display: none;
  transition: all ease 0.1s;
}

.btn__email::before {
  content: "";
  border-width: 10px 8px 0 8px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.6) transparent transparent transparent;
  top: 0px;
  margin-left: -8px;
}

.btn__email::after {
  content: attr(tooltip);
  background: rgba(0, 0, 0, 0.6);
  top: 0px;
  transform: translateY(-100%);
  font-size: 14px;
  margin-left: -75px;
  width: 150px;
  border-radius: 10px;
  color: #ffffff;
  padding: 14px;
}

/* Hover states */

.btn__email:hover::before,
.btn__email:hover::after {
  display: initial;
}

.btn__email:focus {
  outline: none;
}
